/* eslint no-restricted-globals: 0 */
import TfLiteWasm from "./TfLiteWasm";

let tfliteWasm = new TfLiteWasm();
let offCanvas = null;
let bigOffCanvas = null;

if (typeof self.OffscreenCanvas != "undefined") {
  offCanvas = new OffscreenCanvas(192, 192);
  offCanvas.ctx = offCanvas.getContext("2d");

  bigOffCanvas = new OffscreenCanvas(192, 192);
  bigOffCanvas.ctx = bigOffCanvas.getContext("2d");
}

// Handle incoming messages
self.addEventListener(
  "message",
  function (event) {
    const { eventType, eventData, eventID } = event.data;

    if (eventType === "INITIALISE") {
      tfliteWasm.initDone.then(() => {
        // Send back initialised message to main thread
        self.postMessage({
          eventType: "INITIALISED",
        });
      });
    } else if (eventType === "CALL") {
      tfliteWasm.initDone
        .then(() => {
          if (eventData.method === "RUN_INFERENCE") {
            const inputWidth = eventData.arguments[1];
            const inputHeight = eventData.arguments[2];
            let uint8Buffer = null;
            if (eventData.arguments[0] instanceof ImageBitmap && offCanvas) {
              const imageBitmap = eventData.arguments[0];
              offCanvas.ctx.drawImage(
                imageBitmap,
                0,
                0,
                imageBitmap.width,
                imageBitmap.height,
                0,
                0,
                inputWidth,
                inputHeight,
              );
              // Get the image data from the hidden canvas
              const imageData = offCanvas.ctx.getImageData(0, 0, inputWidth, inputHeight);
              uint8Buffer = tfliteWasm.runInference(imageData);
            } else {
              const imageData = new ImageData(
                new Uint8ClampedArray(eventData.arguments[0]),
                eventData.arguments[3],
                eventData.arguments[4],
              );
              if (imageData.width === inputWidth && imageData.height === inputHeight) {
                uint8Buffer = tfliteWasm.runInference(imageData);
              } else {
                if (!bigOffCanvas) {
                  throw Error("TfLiteWorker doesn't support OffscreenCanvas");
                }
                bigOffCanvas.width = imageData.width;
                bigOffCanvas.height = imageData.height;
                bigOffCanvas.ctx.putImageData(imageData, 0, 0);

                offCanvas.ctx.drawImage(
                  bigOffCanvas,
                  0,
                  0,
                  bigOffCanvas.width,
                  bigOffCanvas.height,
                  0,
                  0,
                  inputWidth,
                  inputHeight,
                );
                // Get the image data from the hidden canvas
                const resizedImageData = offCanvas.ctx.getImageData(0, 0, inputWidth, inputHeight);
                uint8Buffer = tfliteWasm.runInference(resizedImageData);
              }
            }

            self.postMessage(
              {
                eventType: "INFERENCE_RESULT",
                eventData: [uint8Buffer.buffer, inputWidth, inputHeight],
                eventID,
              },
              [uint8Buffer.buffer],
            );
          }
        })
        .catch((error) => {
          self.postMessage({
            eventType: "ERROR",
            eventData: "An error occured executing WASM instance function: " + error.toString(),
            eventID,
          });
        });
    }
  },
  false,
);
